import { downloadImage, previewImage, chooseImage, uploadImage, getLocalImgData } from '@utils/ydk';
import { filterWord } from './config'
import html2canvas from 'html2canvas';
import { mapState } from 'vuex'

export default {
  data() {
    return {
      img: '',
      userName: '',
      imgUrl: '',
      commentUrl: '',
      imgStyle: {},

      loading: true,
    };
  },
  computed: mapState([
    //  this.count  store.state.count
    'dreamObj', 'addressObj'
  ]),
  methods: {
    onGoto() {
      location.href = 'https://ke.youdao.com/wap/?outVendor=wdgkt_gwfw_0605&v=' + new Date().getTime();
    }
  },
  mounted: async function () {
    // var canvas = document.createElement('canvas');
    // var context = canvas.getContext('2d');
    // var scaleBy = getPixelRatio(context);
    // console.log('scaleBy', scaleBy)
    // var w = document.body.clientWidth;
    // var h = document.body.clientHeight;
    // canvas.width = w * scaleBy;
    // canvas.height = h * scaleBy;
    // canvas.style.width = w + 'px';
    // canvas.style.height = h + 'px';
    // context.scale(scaleBy, scaleBy);

    const host = `${location.origin}${location.pathname}`.split('#')[0].replace(/index(\d)?\.html/, '');
    this.imgUrl = `${host}/static/img/result-bg.jpg`;
    let imgHeight = window.document.querySelector('#target').offsetHeight // DOM
    let imgWidth = window.document.querySelector('#target').offsetWidth // DOM
    let scale = window.devicePixelRatio // 

    let name = '';
    // 
    filterWord.forEach((word) => {
      name = name.replace(word, '**');
    });
    // this.userName = name;
    const canvasR = await html2canvas(document.querySelector('#target'), {
      scale: scale,
      width: imgWidth,
      height: imgHeight
    });
    this.img = await canvasR.toDataURL("image/jpeg", 1.0);//base64
  }
};

var getPixelRatio = function (context) {
  var backingStore = context.backingStorePixelRatio ||
    context.webkitBackingStorePixelRatio ||
    context.mozBackingStorePixelRatio ||
    context.msBackingStorePixelRatio ||
    context.oBackingStorePixelRatio ||
    context.backingStorePixelRatio || 1;
  return (window.devicePixelRatio || 1) / backingStore;
};



